.login-wrapper {
    background-color: black;
    min-width:600px;
    border:1px solid rgb(55, 250, 250, 0.493);
    position: fixed;
    border-radius: 10px;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    text-align:center;
    padding: 20px;
 } 

 .login-wrapper .logo {
     max-width: 270px;

 }
.login-title{
    font-weight:bold;
    color: rgb(55, 250, 250, 0.493);
}